<template>
    <div>
        <div class="default-window">
            <span class="font-gary font-13"></span>
        </div>
        <div class="recharge-window">
            <div v-for="(item,index) in rechargeList" :key="index" @click="chooseItem=item.recharge_pro_id" class="recharge-item">
                <div class="recharge" :class="{'active':chooseItem==item.recharge_pro_id}">
                    <img v-if="chooseItem==item.recharge_pro_id" src="@/assets/temp/xuanzhong.png" class="choose" alt="">
                    <div><span class="font-red"></span><span class="price">{{item.price}}</span></div>
                    <div><span>{{item.recharge_name}}</span></div>
                </div>
            </div>
        </div>
        <div class=""></div>
        <div class="default-window">
            <van-button :loading="btnStatus" @click="create" round block type="danger"></van-button>
        </div>
        <div class="default-window text-center">
            <router-link :to="{name:'RechargeLog'}"><span></span></router-link>
        </div>

<!--        <van-divider></van-divider>
        <div class="default-window font-13">
            <div><span>1.2000AA8</span></div>
            <div><span>2.3000S7.5</span></div>
            <div><span>3.5000SHG7</span></div>
            <div><span>4.100004A6</span></div>
            <div><span>5.20000+Commune Beans. CB+15.5</span></div>
        </div> -->
        <van-action-sheet v-model="show">
            <div class=" default-window white flex place">
                <div class="default-window"><span></span></div>
                <van-radio-group v-model="payType">
                    <div class="default-window">
                        <van-radio name="2" checked-color="#07c160"></van-radio>
                    </div>
                    <div class="default-window">
                        <van-radio name="1" checked-color="#07c160"></van-radio>
                    </div>

                </van-radio-group>
            </div>
            <div class="default-window">
                <van-button @click="pay" color="linear-gradient(to right, #ff6034, #ee0a24)" block round></van-button>
            </div>
        </van-action-sheet>

    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "Recharge",
        data() {
            return {
                chooseItem: '',
                rechargeList: [],
                btnStatus: false,
                payType: '2',
                show: false,
            }
        },
        mounted() {
            this.loadRecharge();
        },
        methods: {
            //
            loadRecharge() {
                this.$api('Recharge/lists').then(data => {
                    console.log(data);
                    this.rechargeList = data.recharge_pro_list;
                });
            },
            //
            create() {
                if (this.chooseItem == '') {
                    this.$notify('');
                    return;
                }
                this.show = true;

            },
            pay() {
                this.$toast.loading('');
                this.btnStatus = true;
                let params = {
                    recharge_pro_id: this.chooseItem
                };
                this.$api('Recharge/create', params).then(data => {
                    let params2 = {
                        no: data.no,
                        pay_type: this.payType
                    };
                    /* this.$api('Pay/recharge_pay', params2).then(res => {
                        this.postData(res.response);
                    }) */
                    axios({
                        url: `http://www.communebeans.cn/api/Pay/recharge_pay`,
                        method: 'post',
                        data: params2,
                    }).then(res => {
                        let data = res.data;
                        if (data.status == 0) {
                            this.$notify(data.msg);
                        } else if (data.status == 1) {
                            if (this.payType == '1') {
                                //
                                let url = data.data.url;
                                window.location.href = url;
                            } else {
                                this.postData(data.data.response);
                            }
                        } else if (data.status == 2) {
                            this.$toast.success(data.msg);
                            this.$router.push({
                                name: "Success"
                            });
                        }

                    }).catch(fail => {

                    });
                })

            },
            /**
             * @method:ajax
             */
            postData: function(params) {
                let vm = this;

                let data = JSON.parse(params);
                vm.weixinPay(data);
            },
            /**
             * @method :
             * @param data
             */
            weixinPay: function(data) {
                let vm = this;
                if (typeof WeixinJSBridge == "undefined") {
                    //
                    if (document.addEventListener) {
                        document.addEventListener(
                            "WeixinJSBridgeReady",
                            vm.onBridgeReady(data),
                            false
                        );
                    } else if (document.attachEvent) {
                        document.attachEvent(
                            "WeixinJSBridgeReady",
                            vm.onBridgeReady(data)
                        );
                        document.attachEvent(
                            "onWeixinJSBridgeReady",
                            vm.onBridgeReady(data)
                        );
                    }
                } else {
                    vm.onBridgeReady(data);
                }
            },
            /**
             * @method 
             * @param data:,(APIH5);
             */
            onBridgeReady: function(data) {
                let vm = this;
                WeixinJSBridge.invoke(
                    "getBrandWCPayRequest", {
                        appId: data.appId, //
                        timeStamp: data.timeStamp, //1970
                        nonceStr: data.nonceStr, //
                        package: data.package,
                        signType: data.signType, //
                        paySign: data.paySign //
                    },
                    function(res) {
                        // ,res.err_msgok
                        if (res.err_msg == "get_brand_wcpay_request:ok") {
                            vm.$toast.success("");
                            vm.$router.push({
                                name: "Success"
                            });
                        } else {
                            vm.$notify(",");
                            vm.$router.push({
                                name: "UserCenter"
                            });
                        }
                    }
                );
            },
        }
    }
</script>

<style scoped>
    .price {
        color: #ee0a24;
        font-size: 26px;
        font-weight: bold;
    }

    .choose {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 20px;
        display: block
    }

    .active {
        border: 2px solid #ee0a24 !important;
    }

    .recharge-window {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        padding: 5px;
    }

    .recharge-item {
        padding: 5px;
        width: 50%;
        box-sizing: border-box
    }

    .recharge {
        padding: 20px 10px;
        border: 2px solid #EEEEEE;
        position: relative;
        text-align: center;
    }
</style>



